package adventOfCode2021

import colibri.Subject
import colibri.router._

sealed trait Page
object Page {
  case object Landing extends Page
  case class DayPage(aocDay: AocDay) extends Page
  case object Day18Debug extends Page

  val pages: Map[Int, Option[AocDay]] = Map(
    1 -> Some(Day01),
    2 -> Some(Day02),
    3 -> Some(Day03),
    4 -> Some(Day04),
    5 -> Some(Day05),
    6 -> Some(Day06),
    7 -> Some(Day07),
    8 -> Some(Day08),
    9 -> Some(Day09),
    10 -> Some(Day10),
    11 -> Some(Day11),
    12 -> Some(Day12),
    13 -> Some(Day13),
    14 -> Some(Day14),
    15 -> Some(Day15),
    16 -> Some(Day16),
    17 -> Some(Day17),
    18 -> Some(Day18),
    19 -> Some(Day19),
    20 -> Some(Day20),
    21 -> Some(Day21),
    22 -> None,
    23 -> None,
    24 -> None,
    25 -> None
  )

  val current: Subject[Page] = Router.path
    .imapSubject[Page] {
      case Page.DayPage(day) => Root / "day" / day.toString
      case Page.Landing      => Root
      case Page.Day18Debug   => Root / "day-18-debug"
    } {
      case Root / "day-18-debug" => Page.Day18Debug
      case Root / "day" / num if pages.get(num.toInt).flatten.isDefined =>
        Page.DayPage(pages(num.toInt).get)

      case Root / "day" / _ => Page.Landing
      case _                => Page.Landing

    }
}
